@import url(https://fonts.googleapis.com/css?family=Archivo+Black|Roboto+Mono|Satisfy|Volkhov&display=swap);
/*
Attributes defined in here are globally applied to the entire project. Use with caution! 
*/

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Gfonts import */
.header_Icon__ITDRr {
    width: 75px;
    height: 75px;
    border-radius: 132px 132px 132px 132px;
    margin: 10px;
}

@media (max-width: 500px) {
    .header_Icon__ITDRr {
        width: 40px;
        height: 40px;
    }
}
.navbar_NavBar__1Nsv6 {
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    position: fixed;
    top: 0;
    width: 100%;
    box-shadow: 0px 10px 20px 0px rgba(153,153,153,0.5);

    /* Friendly reminder: css can go away forever, please. Dear god. */
    display: -webkit-flex;
    display: flex;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center
}

.navbar_NavBar__1Nsv6 a {
    color: #000000;
    padding: 14px;
    text-decoration: none;
    font-family: 'Volkhov', serif;
    font-size: 18px;
}

.navbar_NavBar__1Nsv6 a:hover{
    transition: .3s ease-out;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    color: rgb(42, 167, 59);
}
/*
Primary Content Container, render things like content plates in here.
*/

.layout_Container__i8TK4 {
    margin-top: 95px;
    background-color: #d6d6d6;
    height: calc(100vh - 95px);
}

@media(max-width:500px) {
    .layout_Container__i8TK4 {
        margin-top: 60px;
        height: calc(100vh - 60px);
    }
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

